import React from 'react';
import { useNavigate } from 'react-router-dom';
import { Menu } from 'antd';
import '../style/common.less';
function Navbar() {
    let navigate = useNavigate();
  
    const handleNavigate = (params) => {
      let key=params.key
      let path=menuItems[key-1].path
      navigate(path);
    };
    const menuItems=[
      {key:'1',label:'',path:'/'},
      {key:'2',label:'',path:'/farmOasis'},
      {key:'3',label:'',path:'/page1'},
      {key:'4',label:'01',path:'/page2'},
      {key:'5',label:'02',path:'/page2'}
    ]
    
    return (
      <Menu className='flex txt-center Navbar' items={menuItems} onSelect={(e)=>handleNavigate(e)} />
    );
    
  }
  
  export default Navbar;